.create_event_container1 {
  direction: rtl;
  width: 1200px;
  background: white;
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  display: grid;
  grid-template-rows: 8% 92%;
  border-radius: 0.4rem 0.4rem 0 0;
  box-shadow: 0 0.4rem 1.6rem 0 rgba(0, 0, 0, 0.11);
  overflow: hidden;
  margin-bottom: 50px;
}

.create_event_container2 {
  width: 95%;
  margin-left: auto;
  margin-right: auto;
}

.create_event_title {
  background-color: #352649;
  display: flex;
  align-items: center;
  padding: 10px;
}

.create_event_title_container {
  width: 30%;
}

.create_event_title p {
  color: rgb(233, 229, 229);
}

.full_width {
  width: 100%;
}

.create_event_rest_input {
  margin-bottom: 25px;
  padding-right: 10px;
  font-size: 14pt;
  font-family: sahel;
  background: #f7f7f7;
  border: 1px solid #ccc;
  border-radius: 4px;
  height: 60px;
  width: 97%;
}

.event_time_input_container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.create_event_input {
  margin-top: 50px;
}

.event_text_area {
  resize: none;
  margin-bottom: 50px;
  font-family: sahel;
  font-size: 14pt;
  width: 100%;
  height: 300px;
}

.create_event_input input:focus {
  border: 1px solid #353535;
}

.event_text_area:focus {
  border: 1px solid #353535;
}

.create_event_submit {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 25px;
}

.create_event_textarea {
  margin-bottom: 30px;
}

.create_event_search_user {
  width: 35%;
}

.create_event_tittle {
  border-bottom: 1px solid rgb(160, 160, 160);
  padding-bottom: 10px;
  margin-bottom: 20px;
}

.create_event_picture {
  margin-top: 50px;
  width: 100%;
  margin-bottom: 80px;
}

.create_event_picture_content {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.create_event_details {
  margin-bottom: 80px;
}

.create_event_details_content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  position: relative;
  width: 95%;
  margin-left: auto;
  margin-right: auto;
}

.create_event_header_font {
  font-size: 18pt;
  font-weight: bold;
}

.create_event_subtittle_font {
  font-size: 14pt;
  font-family: sahel;
}

.one_info {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  position: relative;
  width: 95%;
  margin-left: auto;
  margin-right: auto;
}

.create_event_rest_1 > :nth-child(3) {
  padding-top: 3%;
}

.create_event_details_row {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
}

.first_row {
  justify-content: space-between;
}

.first_row div {
  width: 40%;
}

.second_row {
  justify-content: space-between;
}

.third_row {
  justify-content: center;
}

.left_field {
  margin-right: 0px;
}

.right_field {
  margin-left: 0px;
}

.create_event_search {
  width: 100%;
  height: 300px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.create_event_search_staff {
  width: 45%;
}

.create_event_search_sponser {
  width: 45%;
}

.create_event_search_content {
  width: 95%;
  margin-top: 50px;
  display: flex;
  flex-wrap: nowrap;
}

.staff_search {
  padding-right: 5%;
  justify-content: flex-start;
}

.sponser_search {
  padding-right: 3%;
  justify-content: space-around;
}

.create_event_search_select {
  position: relative;
  width: 90%;
}

.create_event_add_sponser {
  font-size: 20pt;
  color: black;
  cursor: pointer;
}

.create_event_sponser_modal {
  width: 450px;
  height: 230px;
  z-index: 100;
}

.create_event_sponser_modal input {
  width: 200px;
  height: 30px;
  z-index: 10;
  font-size: 14pt;
}

.create_event_sponser_modal p {
  margin-top: 20px;
  margin-right: 10px;
  color: black;
  font-size: 18pt;
}

.create_event_sponser_modal div {
  width: 400px;
  margin-right: 20px;
  height: 50px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}

.create_event_sponser_modal_name {
  /* border: 1px red solid; */
  margin-bottom: 20px;
  margin-top: 30px;
}

.create_event_sponser_modal_address {
  /* border: 1px green solid; */
  margin-bottom: 30px;
}

.create_event_sponser_modal_picture {
  width: 300px !important;
  height: 150px !important;
  /* border: 1px blue solid; */
  margin-right: 70px !important;
  margin-bottom: 40px;
}

.create_event_sponser_modal_picture img {
  width: 150px;
  height: 150px;
  /* border: 1px rgb(121, 120, 120) solid; */
  box-shadow: 4px 4px 4px 4px #888888;
}

.modal_change_picture {
  margin-right: 30px;
  font-size: 18px;
  font-weight: 500;
  width: 170px;
  border-radius: 4px;
  height: 50px;
  line-height: 40px;

  text-align: center;
  background: rgb(198, 198, 198);
  color: black;
  cursor: pointer;
}

.modal_change_picture:hover {
  background: #858585;
  color: white;
}

.create_event_sponser_modal_submit {
  background: #5e4481;
  margin-right: 170px !important;
  width: 100px !important;
  height: 30px;
  border-radius: 4px;
  cursor: pointer;
}

.create_event_sponser_modal_submit p {
  margin-right: 30px;
  margin-top: 0px;
  color: white !important;
}

.create_event_sponser_modal_submit:hover {
  background: #352649;
}

.capacity_container input {
  width: 180px;
  direction: ltr;
  text-align: center;
}

.capacity_container span {
  font-weight: 100;
  margin-right: 10px;
  font-size: 15pt;
}
